<template>
    <div>
        <navbar/>
        <div class="main-container">
            <div class="dashboard-toolbar">

                <h2>我创建的题集</h2>
                <el-button
                        @click="createVisible=true"
                        type="primary" icon="plus">新建
                </el-button>
            </div>
            <el-table :data="items">
                <el-table-column prop="name"
                                 label="题集名称">

                </el-table-column>
                <el-table-column
                        label="状态"
                        prop="onlineStatus">
                    <template slot-scope="scope">
                        <el-tag v-if="scope.row.onlineStatus == 0">
                            未上线
                        </el-tag>
                        <el-tag
                                effect="dark"
                                v-else>已上线
                        </el-tag>

                    </template>
                </el-table-column>
                <el-table-column
                        label="题集操作"
                        prop="action">
                    <template slot-scope="scope">
                        <el-button @click="$router.push('/editor/exerciseSets/'+scope.row.id)">前往出题</el-button>
                        <el-dropdown @command="command">
                            <el-button style="margin-left: 20px" type="primary">
                                更多菜单<i class="el-icon-arrow-down el-icon--right"></i>
                            </el-button>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item :command="{type:'updateInfo', payload: scope.row}">修改信息
                                </el-dropdown-item>
                                <el-dropdown-item :command="{type:'publish', id:scope.row.id}">发布</el-dropdown-item>
                                <el-dropdown-item :command="{type:'delete', id:scope.row.id}">删除</el-dropdown-item>

                            </el-dropdown-menu>
                        </el-dropdown>
                    </template>
                </el-table-column>
            </el-table>
        </div>


        <!-- 新建dialog -->
        <el-dialog
                title="新建题集"
                width="500px"
                :visible.sync="createVisible">
            <el-form>
                <el-form-item>
                    <el-input v-model="newExerciseSetName"/>
                </el-form-item>
                <el-form-item style="text-align: right">
                    <el-button @click="handleCreate">创建</el-button>
                </el-form-item>
            </el-form>


        </el-dialog>

        <!-- 修改信息-->
        <el-drawer
                width="500px"
                title="修改信息"
                :visible.sync="updateInfoVisible">

            <el-form style="padding: 20px">
                <el-form-item>
                    <el-input v-model="editingExerciseSetInfo.name"/>
                </el-form-item>
                <el-form-item>
                    <el-input
                            type="textarea"
                            rows="4"
                            v-model="editingExerciseSetInfo.description"/>
                </el-form-item>
                <el-form-item>
                    <el-button @handle="save">
                        保存
                    </el-button>
                </el-form-item>
            </el-form>
        </el-drawer>
    </div>
</template>

<script>
    import Navbar from "../../components/Navbar";
    import {
        createExerciseSetApi, deleteExerciseSetApi,
        fetchExerciseSetsApi,
        publishExerciseSetApi,
        updateExerciseSetApi
    } from "../../requests/exercises";

    export default {
        name: "ExerciseSets",
        components: {Navbar},
        data() {
            return {
                items: [],
                createVisible: false,
                updateInfoVisible: false,
                editingExerciseSetInfo: {},
                newExerciseSetName: ""
            }
        },
        mounted() {

            this.fetchData()
        },
        methods: {
            command(cmd) {
                switch (cmd.type) {
                    case "updateInfo":
                        this.updateInfoVisible = true
                        this.editingExerciseSetInfo = cmd.payload

                    case "publish":
                        this.publish(cmd.id)

                    case "delete":
                        this.delete(cmd.id)
                }
            },
            fetchData() {
                fetchExerciseSetsApi({all: true}).then(response => {
                    this.items = response.data.results
                })
            },
            save() {
                updateExerciseSetApi(this.$route.params.id, this.editingExerciseSetInfo)
                    .then(response => {

                    })
            },
            handleCreate() {
                createExerciseSetApi({
                    name: this.newExerciseSetName
                }).then(response => {
                    this.createVisible = false
                    this.$message.success("创建成功")
                    this.fetchData()
                })
            },
            publish(id) {
                this.$confirm('是否发布该题集,', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    publishExerciseSetApi(id).then(response=>{
                        this.$message({
                            type: 'success',
                            message: '发布成功!'
                        });
                        this.fetchData()
                    })
                })
            },
            delete(id){
                this.$confirm('是删除该题集,', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    deleteExerciseSetApi(id).then(response=>{
                        this.$message.success("删除成功")
                        this.fetchData()
                    })
                })

            }
        }
    }
</script>

<style scoped>

    .dashboard-toolbar {
        display: flex;

        justify-content: space-between;
        align-items: center;
    }
</style>